  #order-detail-content {
    #cart_summary {
      td {
        position: relative;
        width: 100% !important;
        float: left;
        text-align: left;
        white-space: normal;
        &.cart_product {}
        &.cart_description {}
        &.cart_avail {
          clear: both;
        }
        &.cart_unit {
          .price {
            text-align: left;
          }
        }
        &.cart_quantity {
          clear: both;
        }
        &.cart_total {}
        &.cart_delete {
          width: 100%;
          clear: both;
          text-align: right;
          &:before {
            display: inline-block;
            padding-right: 0.5em;
            position: relative;
            top: -3px;
          }
        }
      }
    }
  }
  .delivery_options{
    table{
      td{
        width: 100% !important;
        text-align: left;
        white-space: normal;
      }
    }
  }
  .resp_table{
    //@include clearfix();
    .resp_table:before, .resp_table:after {
      content: " ";
      /* 1 */
      display: table;
      /* 2 */ }
    .resp_table:after {
      clear: both; }
    //
    // Ensure the content doesn't wrap
    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th,
        > td {
          white-space: nowrap;
        }
      }
    }
    td:before {
      content: attr(data-title);
      display: block;
      font-weight: bold;
      float: left;
    }
    td{
      &.white_space_normal{
        white-space: normal;
      }
      .mobile_table_content{
        min-height: 16px;
        margin-left: 130px;
        text-align: left;
        border-bottom: none;
        white-space: normal;
      }
    }
    thead tr {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }
    table, thead, tbody, th, td, tr {
      display: block;
    }
    tr {
      border-bottom: 1px solid #cccccc;
      overflow: hidden;
    }
    &.table-bordered {
      // Nuke the appropriate borders so that the parent can handle them
      > thead,
      > tbody,
      > tfoot {
        > tr {
          > th,
          > td {
            border: none;
            border-top: 1px solid #D6D4D4;
          }
        }
      }
    }
  }

